import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Paper                                        from 'material-ui/Paper';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';







/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionSupportCustomer, { ReduxEnumSupportCustomer } from '../../../../_redux/action/reduxAction.supportCustomer';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../../_helper/h.navigate';
import HelperStorage		                        from '../../../_helper/h.storage';









/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';









/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../../_component/loading/cc.loading';





import './s.support.customer.list.css';







export default connect(
    ( state ) => {

        let thisState = state[ 'ReduxSupportCustomer' ] ;
        let stateObj = { };

        if ( thisState ) {

            for ( let i = 0 ; i < thisState.length; i ++ ) {
                let recordItem = thisState[ i ];
                if ( !recordItem[ 'completed' ] ) {

                    if ( ReduxEnumSupportCustomer.INIT_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    } else if ( ReduxEnumSupportCustomer.SEARCH_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    }


                    recordItem[ 'completed' ] = true;
                }
            }

        }

        return stateObj;
    }
) (
    class ComponentSupportCustomerList extends ComponentAbstract {



        constructor( props ) {
            super( props )

            console.log( 'ComponentSupportCustomerList : constructor : *** : [' + this.uuid() + '] ' , props );

            this.state = {
                isLoading           : false

                , form              : {
                    cifNumber           : ''
                    , companyName       : ''
                    , companyId         : ''
                }

                , result            : []
            }

        }








        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    listener
            ################################################################################################################################################
            ################################################################################################################################################
        */

       _onReady() {
            console.log( 'ComponentSupportCustomerList : _onReady : *** ');

            this.requestInit();
        }







        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    event
            ################################################################################################################################################
            ################################################################################################################################################
        */

        onClickDetail( cifNumber ) {
            console.log( 'ComponentSupportCustomerList : onClickDetail : *** : cifNumber >> ' + cifNumber );

            HelperNavigate.route(
                HelperNavigate.mapPath( HelperNavigateENUM.SUPPORT_CUSTOMER_DETAIL )
                , {
                    cifNumber : cifNumber
                }
            );
        }

        onClickClear( ) {
            console.log( 'ComponentSupportCustomerList : onClickClear : *** ' );


            let form = this.state.form;
            form[ 'cifNumber' ]     = '';
            form[ 'companyName' ]   = '';
            form[ 'companyId' ]     = '';
            this.setState( {
                form : form
            } );


        }










        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    request
            ################################################################################################################################################
            ################################################################################################################################################
        */
        requestInit( ) {
             console.log( 'ComponentSupportCustomerList : requestInit : *** ' );

             Promise.all( [
                 ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                 , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
             ] ).then(
                 ( value ) => {

                     this.setState(
                         { isLoading: true }
                         , () => {
                             this.dispatch(
                                 ReduxActionSupportCustomer.rInit(
                                     { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                     , {}
                                 )
                             )
                         }
                     );

                 }
             )

         }
         requestInitDone( result ) {
            console.log( 'ComponentSupportCustomerList : requestInitDone : *** : result => ', result );

            this.setState( { isLoading: false } );
            ReduxActionSupportCustomer.rRequestEnd()
        }


        requestList( ) {
            console.log( 'ComponentSupportCustomerList : requestList : ***' )

            Promise.all( [
                ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
            ] ).then(
                ( value ) => {

                    this.setState(
                        { isLoading: true }
                        , () => {

                            this.dispatch(
                                ReduxActionSupportCustomer.rSearch(
                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    , {
                                        companyId       : ''
                                        , cifNumber     : ''
                                        , bussName      : ''
                                    }
                                )
                            )

                        }
                    );

                }
            )

        }
        requestListDone( result ) {
            console.log( 'ComponentSupportCustomerList : requestListDone : *** : result => ', result );
            this.setState( {
                isLoading: false
                , result : result
            } );
            this.dispatch( ReduxActionSupportCustomer.rRequestEnd() );
        }












        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    render
            ################################################################################################################################################
            ################################################################################################################################################
        */

        render() {
            return (
                <div>

                    <Paper>

                        <div style={ { display: 'flex', justifyContent: 'flex-start', padding: 25 } }>
                            <h4 style={ { flex: 1 } }> Customer Support </h4>
                        </div>


                        <div>

                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col md={ 2 } >
                                        CIF Number
                                    </Col>

                                    <Col md={ 10 } >
                                        <TextField
                                            value={ this.state.form.cifNumber }
                                            onChange={
                                                ( event , newValue ) => {
                                                    let form = this.state.form;
                                                    form[ 'cifNumber' ] = newValue;
                                                    this.setState( { form : form } );
                                                }
                                            }
                                        />
                                    </Col>
                                </Row>
                            </Grid>

                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col md={ 2 } >
                                        Company name
                                    </Col>

                                    <Col md={ 10 } >
                                        <TextField
                                            value={ this.state.form.companyName }
                                            onChange={
                                                ( event , newValue ) => {
                                                    let form = this.state.form;
                                                    form[ 'companyName' ] = newValue;
                                                    this.setState( { form : form } );
                                                }
                                            }
                                        />
                                    </Col>
                                </Row>
                            </Grid>

                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col md={ 2 } >
                                        Company ID
                                    </Col>

                                    <Col md={ 10 } >
                                        <TextField
                                            value={ this.state.form.companyId }
                                            onChange={
                                                ( event , newValue ) => {
                                                    let form = this.state.form;
                                                    form[ 'companyId' ] = newValue;
                                                    this.setState( { form : form } );
                                                }
                                            }
                                        />
                                    </Col>
                                </Row>
                            </Grid>

                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col    md={ 12 }
                                            className={ ' support-customer-list-textAlign-center support-customer-list-paddingBottom-30 ' }>

                                        <RaisedButton
                                                            className={ ' support-customer-list-marginLeft-20 support-customer-list-marginRight-20 ' }
                                                            label={ "Clear" }
                                                            onClick={ () => {
                                                                this.onClickClear();
                                                            } }
                                       />



                                       <RaisedButton
                                                            className={ ' support-customer-list-marginLeft-20 support-customer-list-marginRight-20 ' }
                                                            label={ "Search" }
                                                            onClick={ () => {
                                                                this.requestList();
                                                            } }
                                      />

                                    </Col>
                                </Row>
                            </Grid>

                        </div>



                        <div>

                            <Table
                                selectable={ false }
                                fixedHeader={ true }
                                style={ { width: "100%" } } tableLayout="auto">

                                    <TableHeader
                                        adjustForCheckbox={ false }
                                        displaySelectAll={ false }>

                                        <TableRow>

                                            <TableHeaderColumn> CIF Number </TableHeaderColumn>
                                            <TableHeaderColumn> Company ID </TableHeaderColumn>
                                            <TableHeaderColumn> Company Name </TableHeaderColumn>


                                        </TableRow>

                                    </TableHeader>

                                    <TableBody displayRowCheckbox={ false }>
                                        {
                                            this.state.result.map(
                                                ( item , index ) => {

                                                    return (
                                                        <TableRow
                                                                    key={ '_support_customer_' + index }>

                                                            <TableRowColumn>
                                                                <a
                                                                        href={ 'javascript:void( 0 )' }
                                                                        onClick={ () => {
                                                                            this.onClickDetail( item.cifNumber );
                                                                        } } >
                                                                    { item.cifNumber }
                                                                </a>
                                                            </TableRowColumn>
                                                            <TableRowColumn> { item.companyId } </TableRowColumn>
                                                            <TableRowColumn> { item.bussName } </TableRowColumn>

                                                        </ TableRow>
                                                    )
                                                }
                                            )
                                        }
                                    </TableBody>

                                </Table>

                            </div>
                        </Paper>

                        <ComponentCustomLoading
                            visible={ this.state.isLoading }
                        />

                    </div>
                )
            }
    }

)



